import React, { Component } from 'react';
import { connect } from "react-redux";
import { baseUrl } from '../../../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'

class Networks extends Component {
    // constructor() {
    //     super();
    //     //this.state = {showModalType:'addFeed' };
    // }

    uploadType = (uploadType) => {
        this.props.uploadTypeCallback(uploadType)
    };
    render() {
        const { networkData } = this.props;
        return <PerfectScrollbar>
            <div className="addSourceNetworkList px-3">
                <div className="mb-4">
                    <input type="text" className="form-control" placeholder="Search your source..." />
                </div>
                <button type="button" className="btn btn-active-light border border-dashed py-3 w-100 mb-2 fs-7 fw-medium" onClick={() => this.uploadType('uploadAssets')}>
                    <span className="svg-icon">
                        <svg xmlns="http://www.w3.org/2000/svg" width="18.674" height="15.244" viewBox="0 0 18.674 15.244">
                            <g id="Group_9862" data-name="Group 9862" transform="translate(-1638.727 -23.184)">
                                <g id="noun-upload-5032244" transform="translate(1638.726 23.184)">
                                    <path id="Path_7162" data-name="Path 7162" d="M13962.508-3301.471a2.465,2.465,0,0,1-2.461-2.464v-3.51h1.56v3.51a.9.9,0,0,0,.9.9h13.748a.9.9,0,0,0,.9-.9v-3.51h1.563v3.51a2.452,2.452,0,0,1-.724,1.742,2.453,2.453,0,0,1-1.741.722Zm6.1-3.34v-9.241l-3.354,3.353-1.1-1.1,4.681-4.685a.792.792,0,0,1,.552-.227.783.783,0,0,1,.554.227l4.684,4.685-1.1,1.1-3.351-3.353v9.241Z" transform="translate(-13960.047 3316.716)" fill="currentColor" />
                                </g>
                            </g>
                        </svg>
                    </span>
                            Browsee From Device
                        </button>
                {
                    networkData && networkData.length > 0 ? networkData.map((nItem, index) => {
                        return <button key={nItem.id} type="button" className="btn w-100 btn-active-light btn-sm mb-1 fs-7 fw-medium text-start d-flex align-items-center" onClick={() => this.uploadType('addFeed')}>
                            <span className="symbol symbol-24 me-2">
                                <img className="object-center-contain" src={baseUrl(`media/icons/social-icons-square/${String(nItem.icon).replace("fa-", "")}.svg`)} alt="" height={30} width={30} />
                            </span>
                            {nItem.name}
                        </button>
                    }) : null
                }

            </div>
        </PerfectScrollbar>
    }
}


const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData
    }
}
export default connect(mapStateToProps)(Networks);